


  
  .card{
    position: relative;
  }

  .card img{
    width: 100%;
    display: block;
    border: 2px solid #fff;
    border-radius: 6px;
    background-color: white;
  }

  /* front of the card - the picture */
  .card .front{
    transform: rotateY(90deg);
    position: absolute;
  }
  .flipped .front{
    transform: rotateY(0deg);
    
  }
/* back of the card - cover */
.card .back{
    transition: all ease-in 0.2s;
    transition-delay:0.2s;
}
.flipped .back{
    transform: rotateY(90deg);
    transition-delay:0s ;
}